import React from "react";
import PropTypes from "prop-types";

const Instructor = (props) => {
  const {
    instructorName,
    instructorField,
    instructorDesc,
    instructorImg,
    studiedPeople,
    style,
  } = props;

  const instructorStyle = {
    ...style,
    backgroundImage: `url(${instructorImg})`,
    backgroundRepeat: "no-repeat",
    backgroundPosition: "center",
    backgroundSize: "120%",
  };

  return (
    <div className="instructor relative" style={instructorStyle}>
      <div className="absolute people-studied-instructor flex">
        <div className="people-studied-triangle" />
        <div className="people-studied-count">{studiedPeople}学习过</div>
      </div>
      <div className="instructor-intro absolute">
        <div className="instructor-profile flex flex-center ">
          <h4 className="instructor-name">{instructorName}</h4>
          <span className="instructor-focus">{instructorField}</span>
        </div>
        <p className="instructor-desc justify-text">{instructorDesc}</p>
      </div>
    </div>
  );
};

Instructor.propTypes = {
  instructorName: PropTypes.string.isRequired,
  instructorField: PropTypes.string.isRequired,
  instructorDesc: PropTypes.string.isRequired,
  instructorImg: PropTypes.string,
  studiedPeople: PropTypes.number,
};
export default Instructor;
