import React from "react";
import { sideBar } from "../../../constants/home";

const HomeScroll = () => {
  const getSideList = () => {
    return (
      <ul>
        {sideBar.map((content) => (
          <li key={content.title}>
            <a href={content.url}>{content.title}</a>
          </li>
        ))}
      </ul>
    );
  };

  return <aside className="homepage-scroll">{getSideList()}</aside>;
};

export default HomeScroll;
