import React from "react";
import SubHeader from "../subHeader";
import Instructor from "./instructor";

import { homepageInstructors } from "../../../constants/instructor";
import graduation from "../../../asset/img/home/graduation.jpg";

const renderInstructorInRow = (instructorList, numPerRow) => {
  const style = {
    marginRight: 0,
  };
  const tableOfInstructor = [];
  let currCourseIndex = 0;

  for (let row = 0; row < Math.ceil(instructorList.length / numPerRow); row++) {
    const rowOfInstructors = [];
    // 新的逻辑
    if (instructorList.length - currCourseIndex <= numPerRow) {
      rowOfInstructors.push(
        <div className="graduation-img" key="graduation-img">
          <img src={graduation} alt="graduation" />
        </div>
      );
    }
    // 新的逻辑
    for (
      let col = 0;
      col < numPerRow && currCourseIndex < instructorList.length;
      col++
    ) {
      const currInstructor = instructorList[currCourseIndex++];
      if (currInstructor) {
        rowOfInstructors.push(
          <Instructor
            {...currInstructor}
            style={
              // 新的逻辑
              col === numPerRow - 1 || currCourseIndex === instructorList.length
                ? style
                : null
            }
            key={currInstructor.instructorName}
          />
        );
      }
    }

    Math.random();

    tableOfInstructor.push(
      <div className="flex" key={`row${row}`}>
        {rowOfInstructors}
      </div>
    );
  }
  return tableOfInstructor;
};

const InstructorList = (props) => {
  const { subHeaderName } = props;

  return (
    <div>
      <SubHeader subHeaderName={subHeaderName} />
      <div className="instructor-list">
        {renderInstructorInRow(homepageInstructors, 5)}
      </div>
    </div>
  );
};

export default InstructorList;
