class Course {
  // 偷一下懒，学习人数和等级写死了
  // 也可以传到构造函数中去，但是PSD上都一样，我就……
  peopleStudying = 1125;
  courseLevel = "高级";
  constructor(id, title, img, isNew = false, isHot = false) {
    this.id = id;
    this.title = title;
    this.img = img;
    this.isNew = isNew;
    this.isHot = isHot;
  }
}

