// sample data for homePage
export const courseSuggestion1 = () => {
  const list = [];
  for (let i = 0; i < 10; i++) {
    const modVal = i % 5;
    let course = null;
    switch (modVal) {
      case 0:
        course = newPhpCourse(i);
        break;
      case 1:
        course = newAndroidCourse(i);
        break;
      case 2:
        course = newAngularCourse(i);
        break;
      case 3:
      case 4:
        course = newAndroidHybridCourse(i);
        break;
      default:
        break;
    }
    if (i === 0) {
      course.isHot = true;
    } else if (i === 1) {
      course.isNew = true;
    }
    list.push(course);
  }
  return list;
};

