function getRandomInt(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min) + min); //The maximum is exclusive and the minimum is inclusive
}

export const courselist = () => {
  const fullList = [
    newPhpCourse,
    newAndroidCourse,
    newAngularCourse,
    newAndroidHybridCourse,
    newAndroidCourse,
    newUGUI,
    newCocos,
    newUnity,
    newMS,
    newKami,
  ];
  const list = [];
  for (let i = 0; i < 20; i++) {
    const int = getRandomInt(0, fullList.length);
    list.push(fullList[int](i));
  }
  list[0].isHot = true;
  list[1].isNew = true;
  return list;
};
